import React, { Component } from 'react';
import "./style.css"
import MicIcon from '@material-ui/icons/Mic';
import MicOffIcon from '@material-ui/icons/MicOff';
import VideocamIcon from '@material-ui/icons/Videocam';
import VideocamOffIcon from '@material-ui/icons/VideocamOff';
import GetAppIcon from '@material-ui/icons/GetApp';
import AddIcon from '@material-ui/icons/Add';
import IconButton from '@material-ui/core/IconButton';

class RightBar extends Component {
    state = {  }
    render() { 
        return ( <div className="rightbar">
            <IconButton><VideocamIcon className="rightbar-icons"/></IconButton>
            <div className="line"></div>
            <IconButton><MicIcon className="rightbar-icons"/></IconButton>
            <div className="line"></div>
            <IconButton><GetAppIcon className="rightbar-icons"/></IconButton>
            <div className="line"></div>
            <IconButton><AddIcon className="rightbar-icons"/></IconButton>
            <div className="line"></div>
        </div> );
    }
}
 
export default RightBar;