import React, { Component } from 'react';
import './style.css'
import { post } from '../../api-handler';
import { Redirect } from 'react-router-dom';
import io from "socket.io-client";

class Dashboard extends Component {
    componentWillMount(){
        this.socket = io.connect()
    }
    state = { 
        url:"",
        redirectToCodeShare:false,
        data : [
            {
                url : "/reherht",
                title : "fjfjfhlhtrgtgp",
                syntax : "Plain Text",
                Modified: new Date().toISOString().split("T")[0],
                created : new Date().toISOString().split("T")[0],
                actions: "delete icon"
            },
            {
                url : "/reherht",
                title : "fjfjfhlhtrgtgp",
                syntax : "Plain Text",
                Modified: new Date().toISOString().split("T")[0],
                created : new Date().toISOString().split("T")[0],
                actions: "delete icon"
            },
            {
                url : "/reherht",
                title : "fjfjfhlhtrgtgp",
                syntax : "Plain Text",
                Modified: new Date().toISOString().split("T")[0],
                created : new Date().toISOString().split("T")[0],
                actions: "delete icon"
            },
            {
                url : "/reherht",
                title : "fjfjfhlhtrgtgp",
                syntax : "Plain Text",
                Modified: new Date().toISOString().split("T")[0],
                created : new Date().toISOString().split("T")[0],
                actions: "delete icon"
            }
        ],
        sortColumn: {order: "asc"}
     }
     
     renderTableHeader = () => {
         var tableHeader = Object.keys(this.state.data[0])
         return tableHeader.map((key, index) => <th key= {index}>{key.toUpperCase()}</th> )
     }
     renderTableData = () => {
     return this.state.data.map((data, index) => {
            var {url, title, syntax, Modified,  created, actions} = data
            return  (
             <tr key = {index}>
                 <td>{url}</td>
                 <td>{title}</td>
                 <td>{syntax}</td>
                <td>{Modified}</td>
                 <td>{created}</td>
             <td>{actions}</td>
            </tr>
            )
         })
     }
    ShareCodeNow=()=>{
        post('/newcodeshare',{userId:this.props.user._id}).then((res)=>{
            this.setState({
                url:res.code.URL,
                redirectToCodeShare:true
            })
            this.socket.emit('roomcreated',res.code.URL)
        })
    }
    render() { 
        if(this.state.redirectToCodeShare) return <Redirect to={"/code/"+this.state.url}/>
        return (
             <div className = "code-screen">
            <div className="codeshare-heading">
            <p>Your Codeshares</p>
            </div>
            <div className="codeshare-button">
                <button onClick={this.ShareCodeNow}>New Codeshare</button>
            </div>
            <table className = "data">
                <thead>
                    {this.renderTableHeader()}
                    
                </thead>
                <tbody>
                    {this.renderTableData()}
                </tbody>
            </table>
        </div> );
    }
}
 
export default Dashboard;